body {
  background-color: #d1defa;
  font-family: sans-serif, Arial, Helvetica;
  text-align: center;
  padding: 0px;
  margin: 0px;
}

.layout {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-areas: 
      "header header header header"
      "nav nav nav nav"
      "content content content lateral"
      "footer footer footer footer"
  ;

  grid-template-rows: 100px auto auto 70px;
}

/* HEADER*/

.header {
  grid-area: header;
  background-color: white;
  text-align: left;
  font-style: italic;

  display: flex;
  justify-content: center;
}

.header h1 {
  margin-left: 15px;
  margin: 30px;
}

.logo {
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);
  width: 40px;
  height: 40px;
  border-radius: 300px;
  margin-top: 30px;
}


.play {
  width: 0;
  height: 0;
  margin-top: 15px;
  margin-left: 16px;
  border-left: 12px solid white;
  border-top:  5px solid transparent;
  border-bottom: 6px solid transparent;
}


/* BARRA DE NAVEGACIÃ“N*/

.nav {
  grid-area: nav;
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%)
}

.nav > ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.nav > ul > li {
  margin: 10px;
  padding: 10px;
  color: white;
  font-size: 18px;
  font-weight: lighter;
  transition: all 300ms;
}

.nav > ul > li:hover {
  transform: scale(1.1, 1.1);
}

/*CONTENIDO PRINCIPAL */

.content {
  grid-area: content;
  background-color: #f6f7f0;

  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.peli-item {
  margin: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
  flex-grow: 1;
  padding-top: 60px;
  padding-right: 25px;
  padding-bottom: 60px;
  padding-left: 25px;
  padding-top: 25px;
}

.peli-item .edit {
  margin-right: 4px ;
}

button,
input[type="submit"],
input[type="button"] {
  width: 100px;
  background: #0b70e1;
  color: white;
  border: 0;
  outline: 0;
  padding: 15px;
  border-radius: 15px;
  margin-top: 15px;
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 500px;
  cursor: pointer;
  transition: all 300ms;
}

.peli-item .edit:hover {
  background-color: #e77b00;
}

.delete:hover {
  background: #9b0202;
}

/* BARRA LATERAL*/

.lateral {
  grid-area: lateral;
  background: #f6f7f9;
  border-left: 1px solid #ddd;
  padding: 20px;
}

.lateral > div {
  box-sizing: border-box;
  width: 90%;
  margin: 20px;
  padding: 20px;
  padding-top: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
}

.lateral input[type="text"],
.lateral input[type="submit"],
.lateral textarea {
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
}

/*FOOTER*/
.footer {
  grid-area: footer;
  background: #d1defa;
  color: #454545;
  border-top: 1px solid white;
  text-align: left;
  line-height: 68px;
  padding-left: 25px;
}

/*EDITAR PELICULAS*/
.edit_form {
  display: block;
  text-align: center;
  justify-content: center;
  
} 
.titulo_editado {
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
  

}

.descripcion_editada {
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
}

.no-encontrado {
  color: red;
}